<template>
  <div id="map"></div>
</template>

<script>
import maplibregl from "maplibre-gl";
export default {
    name: "Map",
    props: ['appData'],
    mounted(){
        maplibregl.accessToken = 'pk.eyJ1IjoibWtlbGxlcjMiLCJhIjoieFdYUUg5TSJ9.qzhP1v5f1elHrnTV4YpkiA';
        this.appData.map = new maplibregl.Map({
            container: 'map', // container id
            style: 'mapbox://styles/mapbox/streets-v11', // style URL
            center: [0, 0], // starting position [lng, lat]
            zoom: 1 // starting zoom
        });

        const vr = this;

        this.appData.map.on('load', function () {

            vr.appData.map.addSource('route', {
                'type': 'geojson',
                'data': vr.appData.lineFeatureCollection
            })

            vr.appData.map.addSource('location', {
                'type': 'geojson',
                'data': vr.appData.pointFeatureCollection
            })


            vr.appData.map.addLayer({
                'id': 'location',
                'type': 'circle',
                'source': 'location',
                'paint': {
                    'circle-color': '#888',
                    'circle-radius': 6
                }
            });

            vr.appData.map.addLayer({
                'id': 'route',
                'type': 'line',
                'source': 'route',
                'layout': {
                    'line-join': 'round',
                    'line-cap': 'round'
                },
                'paint': {
                    'line-color': '#E6F705',
                    'line-width': 8
                }
            });

            navigator.geolocation.watchPosition(vr.getLocation)
        })
    },
    methods:{
        getLocation(e){
            // console.log(e)
            let currentLat = e.coords.latitude;
            let currentLon = e.coords.longitude;            
            this.appData.map.flyTo({center:[currentLon, currentLat], zoom: 18})
            const pointFeature = {
                "type": "Feature",
                "properties": {},
                "geometry": {
                "type": "Point",
                "coordinates": [
                    currentLon,
                    currentLat
                ]
                }
            }
            if(this.appData.lineFeatureCollection.features[0].geometry.coordinates.length > 0){
                let lastLat = this.appData.lineFeatureCollection.features[0].geometry.coordinates[this.appData.lineFeatureCollection.features[0].geometry.coordinates.length-1][1]
                let lastLon = this.appData.lineFeatureCollection.features[0].geometry.coordinates[this.appData.lineFeatureCollection.features[0].geometry.coordinates.length-1][0]
                if((Math.abs(currentLat-lastLat) > 0.00001) || (Math.abs(currentLon-lastLon) > 0.00001)){
                    console.log('big enough change to add')
                    this.appData.pointFeatureCollection.features[0] = pointFeature
                    this.appData.lineFeatureCollection.features[0].geometry.coordinates.push([currentLon,currentLat])
                    this.appData.map.getSource('route').setData(this.appData.lineFeatureCollection);
                    this.appData.map.getSource('location').setData(this.appData.pointFeatureCollection);
                }
            }else{            
                console.log('first point')
                this.appData.pointFeatureCollection.features[0] = pointFeature
                this.appData.lineFeatureCollection.features[0].geometry.coordinates.push([currentLon,currentLat])
                this.appData.map.getSource('route').setData(this.appData.lineFeatureCollection);
                this.appData.map.getSource('location').setData(this.appData.pointFeatureCollection);
            }
        }
    },
}
</script>

<style>
#map {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}
</style>